/* titillium-web-200 - latin */
@font-face {
    font-family: 'Titillium Web';
    font-style: normal;
    font-weight: 200;
    src: url('/fonts/titillium-web/titillium-web-v14-latin-200.eot'); /* IE9 Compat Modes */
    src: local(''),
         url('/fonts/titillium-web/titillium-web-v14-latin-200.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('/fonts/titillium-web/titillium-web-v14-latin-200.woff2') format('woff2'), /* Super Modern Browsers */
         url('/fonts/titillium-web/titillium-web-v14-latin-200.woff') format('woff'), /* Modern Browsers */
         url('/fonts/titillium-web/titillium-web-v14-latin-200.ttf') format('truetype'), /* Safari, Android, iOS */
         url('/fonts/titillium-web/titillium-web-v14-latin-200.svg#TitilliumWeb') format('svg'); /* Legacy iOS */
}

